import './css/Menu.css';
import {
  NavLink,
  BrowserRouter
} from "react-router-dom";
import imgHome from './img/Home.png';
import imgCalendar from './img/Calendar.png';
import imgCategory from './img/Category.png';
import imgProfile from './img/Profile.png';
import imgSetting from './img/Setting.png';
import MenuButton from './MenuButton';
function Menu() {
    return (
      <BrowserRouter>
        <div className="Menu">
          <NavLink to="/"><MenuButton url = {imgHome} text=""/></NavLink>
          <NavLink to="/Schedule"><MenuButton url = {imgCalendar} text=""/></NavLink>
          <NavLink to="/Services"><MenuButton url = {imgCategory} text=""/></NavLink>
          <NavLink to="/Profile"><MenuButton url = {imgProfile} text=""/></NavLink>
          <NavLink to="/Settings"><MenuButton url = {imgSetting} text=""/></NavLink>
          <button className='AddBtn'> </button>
        </div>
      </BrowserRouter>
    );
  }
  
  export default Menu;